.hero{
    width: 100%;
    min-height: 100vh;
    background-image: linear-gradient(60deg, rgba(238, 236, 239, 0.6),rgba(245, 240, 250, 0.6));
    background-position: center;
    background-size: cover;
    align-items: center;
    display: flex;
    justify-content: center;
}

.hero-text{
    text-align: left;
    max-width: 800px;
}

.hero-text h1{
    font-size: 60px;
    font-weight: 600;
}

.hero-text.text-container {
    flex: 1;
    padding-right: 20px; 
  }
  
  /* Image container */
 .hero .image-container {
    flex: 1;
    text-align: right; 
  }
  
 .hero .image-container img {
    margin-top: 80px;
    width: 400px;
    max-width: 100%; 
    height: auto;
  }